<template>
  <el-select
    :value="value"
    @input="handleInput"
  	:multiple="multiple"
  	:disabled="disabled"
    :placeholder="config.placeholder"
    filterable
    :size="size"
    :allow-create="config.allowCreate !== undefined ? config.allowCreate : false"
    :default-first-option="config.defaultFirstOption !== undefined ? config.defaultFirstOption : false"
    ref="select"
    clearable
  >
  	<el-option
  		v-for="item in options"
  		:key="item.id"
  		:label="item.name"
  		:value="item.id"
  	>
  	</el-option>
  </el-select>
</template>


<script>



const config = [
  ['agency_scope', {
    placeholder: '',
    options: [
      { name: '', id: ''},
      { name: '', id: ''},
      { name: '', id: ''},
      { name: '', id: ''},
      { name: '', id: ''},
    ]
  }],
  ['ipr', {
    placeholder: 'IPR',
    options: 'iprOptions',
    refresh: 'refreshIpr',
  }],
  ['patent_type', {
    placeholder: '',
    options: [
      { name: '', id: 1 },
      { name: '', id: 2 },
      { name: '', id: 3 },
      { name: '+', id: 4 },
    ]
  }],
  ['agency_type', {
    placeholder: '',
    options: [
      {id:1, name:"OA"},
      {id:2, name:"OA"},
      {id:3, name:""},
      {id:4, name:""},
      {id:5, name:""},
      {id:6,name:""}
    ]
  }],
  ['file_type', {
    placeholder: '',
    url: '/api/fileTypes',
  }],
  ['group', {
    placeholder: '',
    options: 'groupOptions',
  }],
  ['mail', {
    placeholder: '',
    url: '/api/mailAddress',
    handle (data) {
      return data.list.map(_=>{return {id: _.value, name: _.label}});
    },
    allowCreate: true,
    defaultFirstOption: true,
  }],
  ['tag', {
    placeholder: '',
    url: '/api/tags',
    handle (data) {
      return data.tags.map(_=>{return {id: _.tag, name: _.tag}});
    },
    allowCreate: true,
    defaultFirstOption: true,
  }],
  ['area', {
    placeholder: '',
    options: 'areaData',
  }],
  ['flow_node', {
    placeholder: '',
    url: '/api/flownodes',
    handle: _=>_.flownodes,
    // set: 'setFlowNodes',
    // refresh: 'refreshFlowNodes',
  }],
  ['fee_code', {
    placeholder: '',
    url: '/api/feeCodes',
    handle (data) {
      return data.codes;
    }
  }],
  ['fee_target_income', {
    placeholder: '',
    url: '/api/feeTargets',
    params: {
      debit: 1,
    },
  }],
  ['fee_target_expenditure', {
    placeholder: '',
    url: '/api/feeTargets',
    params: {
      debit: 0,
    }
  }],
  ['progress', {
    placeholder: '',
    url: '/api/progress',
  }],
  ['branch', {
    placeholder: '',
    options: 'branchOptions',
  }]
];

const map = new Map(config);
const dataMap = new Map([
  ['mail', {data: null}],
  ['fee_target_income', {data: null}],
  ['fee_target_expenditure', {data: null}],
  ['file_type', {data: null}],
  ['fee_code', {data: null}],
  ['tag', {data: null}],
  ['flow_node', {data: null}],
  ['progress',{data: null}]
]);

//----------------------------------------------------------------------------

import AxiosMixins from '@/mixins/axios-mixins'

export default {
  name: 'staticSelect',
  mixins: [ AxiosMixins ],
  props: {
    'value': null,
    'multiple': {
      type: Boolean,
      default: false,
    },
    'disabled': {
      type: Boolean,
      default: false,
    },
    'size': {
      type: String,
    },
    'type': null,
  },
  data () {

    let o = dataMap.get(this.type);
    o = o ? o : null;
    
    return {
      cacheData: o,
      options: [],
    }
  },
  computed: {
  	config () {
  		const config = map.get(this.type);
  		return config ? config : this.type;
  	},
    map () {
      const map = new Map ();
      this.options.forEach(_=>{map.set(_.id, _)});

      return map;
    },
    options_vuex () {
      let op = null;
      if( typeof this.config.options === 'string') {
        op = this.$store.getters[this.config.options];
      }

      return op;
    }
  },
  watch: {
    options_vuex (val) {
      this.options = val;
    },
    value (val) {
      this.$refs.select.visible = false;
    }
  },
  methods: {
    handleInput (val) {
      this.$emit('input', val);
    },
    getSelected () {
      const arr = [];
    
      let cv = this.multiple ? this.value : [ this.value ];
      
      cv.forEach(_=>{
        const val = this.map.get(_);
        if(val) {
          arr.push(val);
        }else {
          arr.push({id: _, name: _});
        }
      })

      return arr;
    },
    setOptions () {
      let op = this.config.options;
      

      if(op instanceof Array) {
        
        //
        this.options = op;

      }else if(typeof op === 'string') {
        op = this.options_vuex;
        //vuex,opgetters,
        //Select,,vuex
        if(op === undefined) {
          if(this.config.refresh) {
            this.$store.dispatch(this.config.refresh);  
          }
        }else {
          this.options = op;
        }

      }else if(op === undefined && this.cacheData) {
        
        //,,cacheData,options
        //options
        //options,watch
        if(this.cacheData.data == null) {
          this.cacheData.data = [];
          if(this.config.url) {
            const url = this.config.url;
            const data = this.config.params ? this.config.params : {};
            const success = _=>{
              const handle = this.config.handle;
              const r = handle ? handle(_) : _.list;

              this.cacheData.data.push(...r);
            }

            this.axiosGet({url, data, success});
          }
        }
        this.options = this.cacheData.data;
      }

    }
  },
  created () {
    this.setOptions();
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
</style>


