<template>
  <el-dialog :title="title" :visible.sync="dialogVisible" >
  	<el-form label-width="100px" ref="form" :model="form" :rules="rules">
  		
  		<!-- <el-form-item label="" prop="from">
  			<remote-select type="member" v-model="form.from"></remote-select>
  		</el-form-item> -->

			<el-form-item label="" prop="to">
				<remote-select type="member" v-model="form.to"></remote-select>
			</el-form-item>

			<el-form-item label="" prop="company">
				<el-autocomplete
          v-model="form.company"
          :fetch-suggestions="querySearch"
          placeholder=""
        ></el-autocomplete>
			</el-form-item>

			<el-form-item label="" prop="number">
				<el-input v-model="form.number" placeholder=""></el-input>
			</el-form-item>

			<el-form-item label="" prop="mail_date">
				<el-date-picker v-model="form.mail_date" type="date" placeholder=""></el-date-picker>
			</el-form-item>

      <el-form-item label="" prop="receipt_date" v-if="type != 'add'">
        <el-date-picker v-model="form.receipt_date" type="date" placeholder=""></el-date-picker>
      </el-form-item>

			<el-form-item label="" prop="description">
        <el-input type="textarea" v-model="form.description" placeholder="..."></el-input>
      </el-form-item>
      <el-form-item label="" prop="projects">
				<express-list v-model="form.projects"></express-list>
			</el-form-item>

			<el-form-item style="margin-bottom: 0px;">
				<el-button type="primary" @click="add" v-if="type === 'add'" :disabled="btn_disabled"></el-button>
				<el-button type="primary" @click="edit" v-if="type === 'edit'" :disabled="btn_disabled"></el-button>
			</el-form-item>

  	</el-form>
  </el-dialog>
</template>

<script>
import AxiosMixins from '@/mixins/axios-mixins'
import PopMixins from '@/mixins/pop-mixins'
import RemoteSelect from '@/components/form/RemoteSelect'
import ExpressList from '@/components/form/ExpressList'

export default {
  name: 'inventorListPop',
  mixins: [ AxiosMixins, PopMixins ],
  data () {
		return {
      form: {
        to: '',
        company: '',
        number: '',
        mail_date: '',
        receipt_date: '',
        description: '',
        projects: [],
      },
      rules: {
      
      },
      queryData: [
        { "value": ''},
        { "value": 'EMS'},
        { "value": ''},
        { "value": ''},
        { "value": ''},
        { "value": ''},
        { "value": ''},
        { "value": ''},
        { "value": ''},
      ],
		}
  },
  methods: {
    querySearch(queryString, cb) {
      var queryData = this.queryData;
      var results = queryString ? queryData.filter(this.createFilter(queryString)) : queryData;
      //  callback 
      cb(results);
    },
    createFilter(queryString) {
      return _ => {
        return (_.value.indexOf(queryString.toLowerCase()) === 0);
      };
    }
  },
  components: {  ExpressList, RemoteSelect  },
  URL: '/api/expresses',
  REMINDER_TEXT: '',
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
</style>


