<template>
  <el-dialog :title="title" :visible.sync="dialogVisible" >
  	<el-form label-width="100px" ref="form" :model="form" :rules="rules">
  		
  		<el-form-item label="" prop="name">
  			<el-input v-model="form.name" placeholder=""></el-input>
  		</el-form-item>

			<el-form-item label="" prop="contact">
				<el-input v-model="form.contact" placeholder=""></el-input>
			</el-form-item>

			<el-form-item label="" prop="address">
				<el-input v-model="form.address" placeholder=""></el-input>
			</el-form-item>

			<el-form-item label="" prop="telephone">
				<el-input v-model="form.telephone" placeholder=""></el-input>
			</el-form-item>

			<el-form-item label="" prop="email">
				<el-input v-model="form.email" placeholder=""></el-input>
			</el-form-item>

			<el-form-item label="" prop="status">
        <app-switch type="status" v-model="form.status"></app-switch>
			</el-form-item>

			<el-form-item label="" prop="scope">
        <static-select type="agency_scope" v-model="form.scope" multiple></static-select>
      </el-form-item>
      <el-form-item label="" prop="is_core_partner">
				<app-switch type="is" v-model="form.is_core_partner"></app-switch>
			</el-form-item>

			<el-form-item style="margin-bottom: 0px;">
				<el-button type="primary" @click="add" v-if="type === 'add'" :disabled="btn_disabled"></el-button>
				<el-button type="primary" @click="edit" v-if="type === 'edit'" :disabled="btn_disabled"></el-button>
			</el-form-item>

  	</el-form>
  </el-dialog>
</template>

<script>
import AxiosMixins from '@/mixins/axios-mixins'
import PopMixins from '@/mixins/pop-mixins'
import AppSwitch from '@/components/form/AppSwitch'
import StaticSelect from '@/components/form/StaticSelect'

export default {
  name: 'inventorListPop',
  mixins: [ AxiosMixins, PopMixins ],
  data () {
		return {
      form: {
        name: '',
        contact: '',
        address: '',
        telephone: '',
        email: '',
        scope: [],
        status: 1,
        is_core_partner: 1,
      },
      rules: {
      
      }
		}
  },
  components: { AppSwitch, StaticSelect },
  URL: '/api/agencies',
  REMINDER_TEXT: '',
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
</style>


