<template>
  <div class="main">
  	<table-component :tableOption="tableOption" :data="tableData"></table-component>
  </div>
</template>

<script>
import TableComponent from '@/components/common/TableComponent'
const text1 = '';
const text2 = '';
const text3 = '';
// console.log(this);

export default {
  name: 'copyrightNotice',
  data () {
	return {
		tableOption: {
		  'header_btn': [{
  			type: 'custom',
  			label: '',
  			icon: 'search',
  			click () {
  				alert("");
  			}
	  	},
	  	{
	  			type: 'dropdown',
	  			label: '',
	  			icon: 'message',
	  			items: [
		  			{
		  				text: "",
		  				click () {
		  					alert("");
		  				}
		  			},
		  			{
		  				text: "",
		  				click () {
		  					alert("");
		  				}
		  			}
	  			]
	  		},
	  		{
	  			type: 'custom',
	  			label: '',
	  			icon: 'upload',
	  			click () {
	  				alert("");
	  			}
	  		}],
	  		'columns': [
	  			{ 'type': 'selection' },
	  			{ 'type': 'text', 'label': '', 'prop': 'text1' },
	  			{ 'type': 'text', 'label': '' },
	  			{ 'type': 'text', 'label': ''},
	  			{ 'type': 'text', 'label': ''},
	  			{ 'type': 'text', 'label': ''},
	  			{ 'type': 'text', 'label': ''},
	  			{	'type': 'action', 
	  				'label': '',
		  			'btns': [{
		  			  label: '',
		  			  icon: 'message',
		  			  click (row) {
		  			  	console.log("",row);
		  			  }
	  				},
	  			{
	  				label: '',
	  			  icon: 'close',
	  			  click (row) {
	  			  	console.log("",row);
	  			  }
	  			}]
	  		}]
		  },
		  tableData: [
				{text1, text2, text3, id: 1},
				{text1, text2, text3, id: 2},
				{text1, text2, text3, id: 3},
				{text1, text2, text3, id: 4},
				{text1, text2, text3, id: 5},
				{text1, text2, text3, id: 6},
				{text1, text2, text3, id: 7}
			]
		}
 	},
  components: { TableComponent }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
</style>



