<template>
  <el-dialog :title="title" :visible.sync="dialogVisible" >
  	<el-form label-width="100px" ref="form" :model="form" :rules="rules">
  		
  		<el-form-item label="" prop="name">
  			<el-input v-model='form.name' placeholder=''></el-input>
  		</el-form-item>

			<el-form-item label="" prop="identity">
				<el-input v-model="form.identity" placeholder=""></el-input>
			</el-form-item>

			<el-form-item label="" prop="citizenship">
				<region v-model="form.citizenship"></region>
			</el-form-item>

			<el-form-item label="" prop="mobile">
				<el-input v-model="form.mobile" placeholder=""></el-input>
			</el-form-item>

			<el-form-item label="" prop="email">
				<el-input v-model="form.email" placeholder=""></el-input>
			</el-form-item>

			<el-form-item label="" prop="not_disclose_name">
				<el-checkbox v-model="form.not_disclose_name" :true-label="1" :false-label="0"></el-checkbox>
			</el-form-item>

			<el-form-item label="" prop="given_name">
        <el-input v-model="form.given_name" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="family_name">
				<el-input v-model="form.family_name" placeholder=""></el-input>
			</el-form-item>

			<el-form-item>
				<el-button type="primary" @click="add" v-if="type === 'add'" :disabled="btn_disabled"></el-button>
				<el-button type="primary" @click="edit" v-if="type === 'edit'" :disabled="btn_disabled"></el-button>
			</el-form-item>

  	</el-form>
  </el-dialog>
</template>

<script>
import AxiosMixins from '@/mixins/axios-mixins'
import PopMixins from '@/mixins/pop-mixins'
import Region from '@/components/form/Region'
export default {
  name: 'inventorListPop',
  mixins: [ AxiosMixins, PopMixins ],
  data () {
		return {
      form: {
        name: '',
        identity: '',
        citizenship: '',
        mobile: '',
        email: '',
        not_disclose_name: 0,
        given_name: '',
        family_name: '',
      },
      rules: {
        name: { required: true, message: '', trigger: 'blur' }
      }
		}
  },
  components: { Region },
  URL: '/api/inventors',
  REMINDER_TEXT: '',
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
</style>


