<template>
  <div class="main">
  	<div id="echarts" style="width: 100%; height: 800px;"></div>
  </div>
</template>

<script>
import echarts from 'echarts'

export default {
  name: 'trademarkStatistics',
  data () {
	return {
	  msg: 'Welcome to Your Vue.js App'
	}
  },
  mounted () {
	// domecharts
	const d = this;
	const myChart = echarts.init(d.$el.querySelector("#echarts"));
	// 
	myChart.setOption({
	    title: { text: 'ECharts ' },
	    tooltip: {},
	    xAxis: {
	        data: ["","","","","",""]
	    },
	    yAxis: {},
	    series: [{
	        name: '',
	        type: 'bar',
	        data: [5, 20, 36, 10, 10, 20]
	    }]
		});
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
</style>



