<template>
  <div class="main">
	<table-component :tableOption="tableOption" :data=tableData></table-component>
	<el-dialog :visible.sync="dialogScreenVisible" title="" class="dialog-small">
		<el-form label-width="80px">
			<el-form-item label=""><el-input></el-input></el-form-item>
			<el-form-item label=""><el-input></el-input></el-form-item>
			<el-form-item label=""><el-input></el-input></el-form-item>
			<el-form-item label=""><app-date-picker></app-date-picker></el-form-item>
			<el-form-item label=""><app-date-picker></app-date-picker></el-form-item>
			<el-form-item><el-button></el-button></el-form-item>
		</el-form>
	</el-dialog>
  </div>
</template>

<script>
import TableComponent from '@/components/common/TableComponent'
import AppDatePicker from '@/components/common/AppDatePicker'

const text1 = '';
const text2 = '';
const text3 = '';

export default {
  name: 'trademarkList', 
  data () {
	return {
		dialogScreenVisible: false,
		tableOption: {
			'header_btn': [{
				'type': 'custom',
				'label': '',
				'icon': 'plus',
				click () {
					alert("");
				}
			},
			{
				'type': 'custom',
				'label': '',
				'icon': 'delete',
				click () {
					alert("");
				}
			},
			{
				'type': 'dropdown',
				'label': '',
				'icon': '',
				'items': [{
					text: '',
					click: ()=>{ this.dialogScreenVisible = true; },
				}]
			},
			{
				'type': 'control',
				'label': ''
			}],
			'is_search': false,//true
			'columns': [{
				'show': true,
				'type': 'selection'
			},
			{
				'show': true,//true
				'type': 'text',
				'label': '',
				'prop': 'text1',
			},
		  	{
				'show': true,
				'type': 'text',
				'label': '',
				'prop': 'text2'
		  	},
		  	{
				'show': true,
				'type': 'text',
				'label': '',
				'prop': 'text3'
		  	},
		  	{
				'show': false,
				'type': 'text',
				'label': '',
				'prop': 'text3'
		  	},
		  	{
				'show': false,
				'type': 'text',
				'label': '',
				'prop': 'text3'
		  	},
		  	{
				'show': false,
				'type': 'text',
				'label': '',
				'prop': 'text3'
		  	},
		  	{
				'show': true,
				'type': 'text',
				'label': '',
				'prop': 'text3'
		  	},
		  	{
				'show': false,
				'type': 'text',
				'label': '',
				'prop': 'text3'
		  	},
		  	{
				'show': false,
				'type': 'text',
				'label': '',
				'prop': 'text3'
		  	},
		  	{
				'show': false,
				'type': 'text',
				'label': '',
				'prop': 'text3'
		  	},
		  	{
				'show': true,
				'type': 'text',
				'label': ''
		  	},
		  	{ 'show': false, 'type': 'text', 'label': '' },
		  	{ 'show': false, 'type': 'text', 'label': '' },
		  	{ 'show': true, 'type': 'text', 'label': '' },
		  	{ 'show': true, 'type': 'text', 'label': 'IPR' },
		  	{ 'show': true, 'type': 'text', 'label': '' },
		  	{
				'show': true,
				'type': 'action',
				'label': '',
				'btns': [{
					'label': '',
					'icon': 'view',
					'click': (row)=>{ this.$router.push(`/trademark/list/detail/${row.id}/babel`); }, 
				}]
			}] 
		},
		tableData: [
			{text1, text2, text3, id: 1},
			{text1, text2, text3, id: 2},
			{text1, text2, text3, id: 3},
			{text1, text2, text3, id: 4},
			{text1, text2, text3, id: 5},
			{text1, text2, text3, id: 6},
			{text1, text2, text3, id: 7}
		]
	};
  },
  components: { TableComponent, AppDatePicker }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">

</style>


