<template>
  <app-collapse col-title="">
      <el-form label-width="120px">
        <el-form-item label="" v-if="type == 'edit'">
          {{ form.progress.name }}
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.words" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.remark" type="textarea" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <upload v-model="form.attachments" :file-list="attachments"></upload>
        </el-form-item>
      </el-form>
    </app-collapse>
</template>

<script>
import AppCollapse from '@/components/common/AppCollapse'
import Upload from '@/components/form/Upload'

export default {
  name: 'patentAddOther',
  props: ['type'],
  data () {
		return {
			form: {
        progress: '',
        words: '',
        remark: '',
        attachments: [],
			},
      attachments: [],
		}
  },
  methods: {
  	setForm (data) {
      for(let k in this.form) {
        const d = data[k];
        if(k == 'attachments') {
          this.form[k] = d.map(_=>_.id);
          this.attachments = d;
        }else {
          this.form[k] = d;
        }
      }
  	},
    submitForm () {
      return this.form;
    },
    checkForm (callback) {
      callback(true);
    },
  },
  components: { AppCollapse, Upload }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
</style>


