<template>
  <div class="main">
	<table-component :tableOption="option" :data="tableData"></table-component>
  </div>
</template>

<script>
import TableComponent from '@/components/common/TableComponent' 

export default {
  name: 'hello',
  data () {
	return {
	  option: {
      'header_btn': [
        { type: 'custom', label: '', icon: 'plus', click: ()=>{alert("")} },
        { type: 'custom', label: '', icon: '', click: ()=>{alert("")} },
        { type: 'custom', label: '', icon: '', click: ()=>{alert("")} },
        { type: 'custom', label: '', icon: 'delete', click: ()=>{alert("")} },
      ],
      'columns': [
        { type: 'selection' },
        { type: 'text', label: '' },
        { type: 'text', label: '' },
        { type: 'text', label: '' },
        { 
          type: 'action',
          btns: [
            { label: '', btn_type: 'text', click: ()=>{alert('')} },
            { label: '', btn_type: 'text', click: ()=>{alert('')} },
          ],
        },
      ],
    },
	  tableData: [],
	}
  },
  components: { TableComponent }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
</style>


