<template>
  <div class="main">
  	<el-row>
  		<el-col :span="18">
		  	<el-form label-width="80px">
		  		<el-form-item label="">
		  			<el-select v-model="form.result" placeholder="">
						<el-option value="pass" label=""></el-option>
						<el-option 	value="no_pass" label=""></el-option>
		  			</el-select>
		  		</el-form-item>
					<el-form-item label="">
						<el-input
						  type="textarea"
						  placeholder=""
						  v-model="form.opinion">
						</el-input>
					</el-form-item>
					<el-form-item label="">
						<el-upload
						  class="upload-demo"
						  drag
						  action="https://jsonplaceholder.typicode.com/posts/"
						  multiple>
						  <i class="el-icon-upload"></i>
						  <div class="el-upload__text"><em></em></div>
						  <div class="el-upload__tip" slot="tip">jpg/png500kb</div>
						</el-upload>
					</el-form-item>
					<el-form-item>
					  <el-button type="primary"></el-button>
					  <el-button></el-button>
					</el-form-item>
		  	</el-form>
	  	</el-col>
	  	<el-col :span="6" style="padding-left: 40px;">
				<h3 style="margin-top: 0;"></h3>
				<el-steps :space="100" direction="vertical" :active="1" finish-status="success">
				  <el-step title=""></el-step>
				  <el-step title=""></el-step>
				  <el-step title="IPR"></el-step>
				  <el-step title=""></el-step>
				</el-steps>
	  	</el-col>
  	</el-row>
  </div>
</template>

<script>
export default {
  name: 'checkProposal',
  created () {
  	const d = this;
  	d.fetchData();
  },
  methods: {
  	fetchData() {
  		const d = this;
  		console.log("" + d.$route.query.id);
  	}
  },
  data () {
    return {
      form: {
      	result: null,
      	opinion: null,
      	filders: null
      }
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
</style>



